import {useState, useEffect} from 'react';
import axios from 'axios'
import { Link, useParams } from 'react-router-dom';

import './Pages.scss';

const ActorFilms = ({ actorId }) => {
	let { id } = useParams();


 const [moviesList, setMoviesList ] = useState({
 	isFetched: false,
 	data: [],
 	error: null
 })

   useEffect(() => {
	  axios.get(`https://api.themoviedb.org/3/person/${id}/movie_credits?page=1
`, {
      params: {
       api_key: '4cfe16e4e02141d629e8aa2cb7c23fe6'
    }
  })
  .then(function (response) {
  	console.log(response)
    setMoviesList({
    	isFetched: true,
	 	data: response.data.results,
	 	error: false,
    })
  })
  .catch(function (error) {
	   setMoviesList({
	   	isFetched: true,
	 		data: [],
	 		error: error,
	   })
  })
  .then(function () {
  
  }); 
   }, [])


    return (
        <div className="actor-films">
        		{
        			moviesList.isFetched ? moviesList.data.filter(movie => movie.id === actorId) : (
					<h1>Sabr...</h1>
					)
        		}
         </div>
    )
}

export default ActorFilms;